import React, { Component } from 'react';

import NovList from '../../../component/NovList/novList';
import NovCommend from '../../../component/NovCommend/novCommend';

class ContentNover extends Component {
	render(){
		return (
            <div>
                <NovList id="5912825ba1dbf3ad33ee7ffe" title="" data={this.props.data[0]}/>
                <NovList id="59128334694d1cda365b8985" title="" data={this.props.data[1]}/>
                <NovCommend id="59128365e5a3262c37488e3d" title="" data={this.props.data[2]}/>
                <NovCommend id="5912a9d08973b2fe33614642" title="" data={this.props.data[3]}/>
                <NovCommend id="5912aa17e647570434175d34" title="" data={this.props.data[4]}/>
                <NovList id="5912872f8973b2fe3361463f" title="" data={this.props.data[5]}/>
                <NovList show={true} id="591284376e2e237c36d7b8bd" title="" data={this.props.data[6]}/>
            </div>
		)
	}
}

export default ContentNover;


