import React, { Component } from 'react';

import './commendDetail.less';

class CommendDetail extends Component {
	render() {
		var item = this.props.data;
		// if(item.book.title)
		// var item = {}
		// if(items.title){
  //           item = items
		// }else{
		// 	item = item.book;
		// }
		return (
			<div className="commendDetail-div">
	           	<div className="commendDetail-left">
	           		<img src={/statics/.test(item.cover)?item.cover: 'http://statics.zhuishushenqi.com' + item.cover}/>
	           	</div>
	           	<div className="commendDetail-right">
	           		<p>{item.title}</p>
	           		<p>{item.author} | {item.cat}</p>
	           		<p>{item.shortIntro}</p>
	           		<p><span>{item.latelyFollower}</span> | <span>{ item.retentionRatio }%</span></p>
	           	</div>
	        </div>
		)
	}
}

export default CommendDetail;


